import React from 'react'
import moment from 'moment';
import Moment from 'react-moment';

import TextField from '@material-ui/core/TextField';
import { RadioGroup, RadioButton } from 'react-radio-buttons'

import Layout from '../components/layout'

class IndexPage extends React.Component {
  static displayName = 'TestComponent';

  styles = ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      width: 200,
    },
  });

  state = { time: moment().format('YYYY-MM-DD HH:mm:ss') };
  
  render() {

    return <div>
      <Layout>
        <div style={{marginBottom: 30 + 'px'}}>
          <RadioGroup onChange={(value) => console.log(value)} horizontal value="sgn">
            <RadioButton value="han" pointColor='#639'>Base HAN</RadioButton>
            <RadioButton value="sgn" pointColor='#639'>Base SGN</RadioButton>
          </RadioGroup>

          <h3 style={{ marginTop: 30 + 'px' }}>Chọn giờ bay</h3>
          <form style={this.styles.container} noValidate>
            <TextField
              id="time"
              type="time"
              defaultValue={moment().format('HH:mm')}
              InputLabelProps={{
                shrink: true,
              }}
              inputProps={{
                step: 300, // 5 min
              }}
              style={this.styles.textField}
              //onChange={(e) => console.log(moment(e.target.value, "HH:mm").format('YYYY-MM-DD HH:mm:ss'))}
              onChange={(e) => this.setState({ time: moment(e.target.value, "HH:mm").format('YYYY-MM-DD HH:mm:ss') })}
            />
          </form>
        </div>

        <table style={{width: 100 + '%'}}>
          <tbody>
            <tr>
              <th>Chặng</th>
              <th>Giờ briefing</th>
            </tr>
            <tr>
              <td>Nội địa</td>
              <td><Moment format="HH:mm" subtract={{ hours: 1, minutes: 20 }}>{this.state.time}</Moment></td>
            </tr>
            <tr>
              <td>Ngắn</td>
              <td><Moment format="HH:mm" subtract={{ hours: 1, minutes: 35 }}>{this.state.time}</Moment></td>
            </tr>
            <tr>
              <td>Trung</td>
              <td><Moment format="HH:mm" subtract={{ hours: 1, minutes: 50 }}>{this.state.time}</Moment></td>
            </tr>
            <tr>
              <td>Dài</td>
              <td><Moment format="HH:mm" subtract={{ hours: 2, minutes: 15 }}>{this.state.time}</Moment></td>
            </tr>
          </tbody>
        </table>

        <p style={{ fontSize: 12 + 'px', fontStyle: 'italic', color: '#9BB0C2' }}>Lưu ý: Nội địa có mặt trước 1h20p, Ngắn có mặt trước 1h35p, Trung có mặt trước 1h50p và Dài có mặt trước 2h15p</p>

        <p style={{ fontSize: 12 + 'px', fontStyle: 'italic', textAlign: 'center' }}><span role="img" aria-label="heart">❤️</span> via Ha93</p>
      </Layout>
    </div>
  }
}


export default IndexPage
